import classNames from 'classnames';
import React from 'react';

export interface CardProps extends React.HTMLAttributes<HTMLDivElement> {
  readonly color?: 'light' | 'dark';
  readonly rounded?: 'sm' | 'md' | 'lg';
  readonly padding?: 'none' | 'sm' | 'md';
}

export const Card: React.FC<CardProps> = React.forwardRef<HTMLDivElement, CardProps>(
  ({ color = 'light', padding = 'md', rounded = 'md', className, children, ...props }, ref) => (
    <div
      ref={ref}
      className={classNames(
        '',
        {
          'bg-inverseBg text-inverseBody1 shadow-lg': color === 'light',
          'bg-primary text-body1': color === 'dark',
          'rounded-sm': rounded === 'sm',
          rounded: rounded === 'md',
          'rounded-lg': rounded === 'lg',
          'p-3': padding === 'sm',
          'p-6': padding === 'md',
        },
        className
      )}
      {...props}
    >
      {children}
    </div>
  )
);

Card.displayName = 'Card';
